'use client';

import React, { useState } from 'react';
import { EditorContent, useEditor } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import {
  PaginationPlus,
  PAGE_SIZES,
} from 'tiptap-pagination-plus';
import Table from '@tiptap/extension-table';
import TableRow from '@tiptap/extension-table-row';
import TableCell from '@tiptap/extension-table-cell';
import TableHeader from '@tiptap/extension-table-header';
import Underline from '@tiptap/extension-underline';
import TextStyle from '@tiptap/extension-text-style';
import { FontSize } from './extensions';
import EditorToolbar from './EditorToolbar';
import { useTheme } from '@/hooks/useTheme';

const DocumentEditor: React.FC = () => {
  const { theme, toggleTheme } = useTheme();
  const [documentTitle, setDocumentTitle] = useState('Untitled document');

  const editor = useEditor({
    extensions: [
      StarterKit.configure({
        // Disable default history to avoid conflicts if needed, but usually fine
      }),
      Underline,
      TextStyle,
      FontSize,
      Table.configure({ resizable: true }),
      TableRow,
      TableHeader,
      TableCell,
      PaginationPlus.configure({
        pageSize: PAGE_SIZES.LETTER, // Default to Letter for US users, or A4
        marginTop: 96, // 1 inch = 96px
        marginBottom: 96,
        marginLeft: 96,
        marginRight: 96,
        pageGap: 20,
        pageHeaderHeight: 40,
        pageFooterHeight: 40,
        footerRight: 'Page {page} of {total}',
      }),
    ],
    content: '<p>Start typingâ€¦</p>',
    editorProps: {
        attributes: {
            class: 'min-h-full',
        }
    }
  });

  const handleTitleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      editor?.commands.focus();
    }
  };

  // We keep the toolbar and layout shell, but simplify the editor container significantly
  return (
    <div className="flex flex-col h-screen bg-workspace overflow-hidden">
      {/* Header with title */}
      <div className="no-print flex items-center px-4 py-3 bg-toolbar border-b border-toolbar-border z-20 relative">
        <input
          type="text"
          value={documentTitle}
          onChange={(e) => setDocumentTitle(e.target.value)}
          onKeyDown={handleTitleKeyDown}
          placeholder="Untitled document"
          className="text-lg font-medium bg-transparent border-none outline-none focus:outline-none text-foreground placeholder:text-muted-foreground"
          style={{ minWidth: '200px' }}
        />
      </div>

       {/* Toolbar */}
       <div className="z-20 relative">
        <EditorToolbar
          editor={editor}
          theme={theme}
          onToggleTheme={toggleTheme}
          onExportPDF={() => {}} // Disable PDF export temporarily or impl later
        />
      </div>

      {/* Main scrolling area */}
      <div className="flex-1 overflow-auto bg-gray-200 py-10 flex justify-center cursor-text" onClick={() => editor?.commands.focus()}>
         {/* Editor Content Area - NO wrapper with height! */}
         <EditorContent editor={editor} className="w-auto h-auto" />
      </div>
    </div>
  );
};

export default DocumentEditor;
